.App {
    text-align: center;
    color: #ecebf1;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

a {
    color: #d3d3d3;
    text-decoration: none;
}

textarea {
    border: 3px;
    border-radius: 10px;
    border-style: outset;
    border-color: #232323;
    width: 727px;
    /* height: 54px; */
    height: fit-content;
    display: flex;
    /* align-items: center; */
    margin: 9px auto;
    font-family: Ubuntu;
}

label {
    font-family: Ubuntu;
    font-size: 18px;
    /* display: flex; */
}

h2,
h1 {
    font-family: Ubuntu;
}


