nav {
    padding-bottom: 5px;
}

.navbutton {
    width: 150px;
    height: 30px;
    border-radius: 8px;
    background-color: #25012e;
    font-size: 15px;
    font-family: Ubuntu;
    color: #d3d3d3;
    cursor: pointer;
    /* transition-duration: 0.4s; */
    margin: 10px;
    outline: none;
}

.navbutton:hover {
    background-color: #25012e;
    opacity: 50%;
}

.navbutton.active {
    background-color: red;
}

p {
    margin: 10px 20px 95px;
}

img {
    max-height: 100px;
    margin-top: 50px;
}

Footer {
    display: flex;
    justify-content: center;
    background-color: #1f1f23;
    height: 48px;
}

.container {
    background: #524e4e;
    margin: 6px;
    border-style: solid;
    border-color: #0f0e0e;
    border-radius: 10px;
    width: 760px;
    height: fit-content;
    margin: auto;
}

.container-chat {
    background: #2e2c2c;
    margin: 6px;
    border-style: solid;
    border-color: #0f0e0e;
    border-radius: 10px;
    width: 760px;
    /* height: fit-content; */
    /* text-align: none; */
    /* height: 100%; */
    margin: auto;
}


