.del {
    margin: 20px;
}

.pb {
    padding-bottom: 8px;
    height: 100%;
}

.pt {
    padding-top: 8px;
}

.pp3 {
    color: rgb(191, 64, 64);
    /* margin-left: 8px; */
}

.t {
    font-family: 'Tilt Neon', cursive;
    font-size: 12px;
}

.center {
    display: flex;
    justify-content: center;
    padding-bottom: 9px;
}

.fixed {
    position: fixed;
    /* top: 100dvh; */
    bottom: 8px;
    left: 0;
    right: 0;
}

@media screen and (max-width: 640px) {
    .container {
        width: 320px;
        height: fit-content;
    }
    .container-chat {
        width: 320px;
        height: fit-content;
    }
    textarea {
        width: 300px;
        height: fit-content;
    }
}